.page-header {
  background-color: #1b2d37;
  background-image: url("../img/wires-desktop@1x.png");
  background-size: 100% auto; }
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-header {
      background-image: url("../img/wires-desktop@2x.png"); } }

@media (max-width: 767px) {
  .page-header {
    min-width: 320px; } }

.main-nav {
  max-width: 1410px;
  width: 90%;
  margin: 0 auto;
  padding: 33px 0; }

.main-nav__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none; }

.main-nav__item--logo {
  margin-right: auto; }

.main-nav__item--phone {
  margin-left: 6%; }

.main-nav__item--order {
  margin-left: 3%; }

.main-nav__link {
  display: flex;
  align-items: center;
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer; }
  .main-nav__link:hover {
    opacity: 0.5; }
  .main-nav__link:focus {
    opacity: 0.8; }

.main-nav__icon--email {
  margin-right: 20px; }

.main-nav__icon--phone {
  margin-right: 16px; }

.main-nav__order-button {
  padding: 12px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  border-width: 1px;
  border-style: solid;
  border-radius: 5px;
  padding: 13px 26px;
  color: #ffffff;
  border-color: #ffffff; }
  .main-nav__order-button:hover {
    opacity: 0.5; }
  .main-nav__order-button:focus {
    color: #00b2ff;
    border-color: #00b2ff; }

@media (max-width: 1023px) {
  .main-nav {
    width: 94%;
    padding: 20px 0; }
  .main-nav__item--phone {
    margin-left: 5%; }
  .main-nav__item--order {
    display: none; }
  .main-nav__icon {
    margin-right: 0; }
    .main-nav__icon--email {
      width: 26px;
      height: 19px; }
    .main-nav__icon--phone {
      width: 24px;
      height: 24px; }
  .main-nav__text-link {
    display: none; } }

@media (max-width: 767px) {
  .main-nav {
    width: 90%;
    padding: 17px 0; }
  .main-nav__item--phone {
    margin-left: 7%; }
  .main-nav__link--logo img {
    width: 38px;
    height: 38px; } }
